html {
  --active-link-color: #00acdc;
  --header-height: 60px;
  font-family: Roboto, Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  line-height: 0;
  display: inline-block;
}

.figure__logo-moyo {
  margin: 0;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin: 0;
  padding: 0;
}

.nav__item:not(:first-of-type) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  height: var(--header-height);
  line-height: var(--header-height);
  padding: 0;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

a[href^="#"] {
  color: #000;
}

a.is-active:after {
  content: "";
  background: var(--active-link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:visited {
  color: inherit;
}

.nav__link:hover, a.nav__link.is-active {
  color: var(--active-link-color);
}
